.hero-main{
    color: white;
    background-image: url('../../assets/images/banner-bg.jpg');
    background-size: cover;
    display: flex;
    padding: 150px var(--ge-p-left);
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
    overflow: hidden;
}
@media (max-width: 992px){
    .hero-main{
        flex-direction: column;
    }
}
.hero-main > div{
    flex: .5;
}

.hero-main .first-row h4{
    margin-bottom: 30px;
}
.hero-main .first-row h1{
    font-weight: bold;
}
.hero-main .first-row p{
    line-height: 1.5;
    font-size: 17px;
}
.hero-main .first-row form{
    background-color: white;
    border-radius: 20px;
    overflow: hidden;
    width: fit-content;
}
.hero-main .first-row form input{
    padding: 10px;
    border-style: none;
    width: 250px;
}
.hero-main .first-row form button{
    background-color: rgb(241, 116, 116);
    border-radius: 20px;
    color: white;
    padding: 10px 20px;
    border-style: none;

}
@media (max-width: 992px){
    .hero-main .first-row{
    text-align: center;
}
.hero-main .first-row form{
    margin: 10px auto;
}
}
.hero-main .second-row{
    text-align: right;
    overflow: hidden;
}
.hero-main .second-row img{
    border-radius: 20px;
    max-width:100%;
    height: auto;
}