.trend-main{
    padding: 90px  var(--ge-p-left);
    background-color:var(--gray-bg-section);
    border-radius: 50px;
    margin: 60px 0;
}

.trend-main .trend{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
}
.trend-main .trend .row-trend{
    flex: 0 1 calc(25% - 30px);
    background-color: #eee;
    border-radius: 20px;
    overflow: hidden;
}
@media (max-width:992px){
    .trend-main .trend .row-trend{
        flex: 0 1 calc(50% - 30px);
    }
}
@media (max-width:767px){
    .trend-main .trend .row-trend{
        flex: 0 1 calc(100% - 30px);
    }
}
.trend-main .trend .row-trend img{
    width: 100%;
}
.trend-main .trend .row-trend .info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10px;
}
.trend-main .trend .row-trend .info .bolds{
    font-size: 20px;
    font-weight: bold;
}
.trend-main .trend .row-trend .info .icons{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    border-radius: 50%;
    transition: .4s;
    width: 50px;
    height: 50px;
    color: white;
    background-color: var(--second-color);
}
.trend-main .trend .row-trend .info .icons:hover{
    background-color: var(--main-color);

}