.dwn-more{
    margin-bottom: 15px;
}
.most-info{
    display: block !important ; 
    text-align: center;
}
.trend-main .trend{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
}
.trend-main .trend .row-most{
    flex: 0 1 calc(17% - 30px);
    background-color: #eee;
    border-radius: 20px;
    padding-bottom: 15px;
    overflow: hidden;
}
.trend-main .trend .row-most img{
    width: 100%;
}
@media (max-width:992px){
    .trend-main .trend .row-most{
        flex: 0 1 calc(50% - 30px);
    }
}
@media (max-width:767px){
    .trend-main .trend .row-most{
        flex: 0 1 calc(100% - 30px);
    }
}
