.ourshop .head{
    background-image: url('../../assets/images/banner-bg.jpg');
    text-align: center;
    color: white;
    font-size: 60px;
    font-weight: bold;
    padding-top: 150px;
    padding-bottom: 100px;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
}
.ourshop .content{
    padding: 90px  var(--ge-p-left);
    background-color:var(--gray-bg-section);
    border-radius: 50px;
    margin: 60px 0;
}
.ourshop .btns{
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}
.ourshop .trend{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}
.ourshop .trend .row-trend{
    flex: 0 1 calc(25% - 30px);
    background-color: #eee;
    border-radius: 20px;
    overflow: hidden;
}
@media (max-width:992px){
    .ourshop .trend .row-trend{
        flex: 0 1 calc(50% - 30px);
    }
}
@media (max-width:767px){
    .ourshop .trend .row-trend{
        flex: 0 1 calc(100% - 30px);
    }
}
.ourshop .trend .row-trend img{
    width: 100%;
}
.ourshop .trend .row-trend .info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10px;
}
.ourshop .trend .row-trend .info .bolds{
    font-size: 20px;
    font-weight: bold;
}
.ourshop .trend .row-trend .info .icons{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    border-radius: 50%;
    transition: .4s;
    width: 50px;
    height: 50px;
    color: white;
    background-color: var(--second-color);
}
.ourshop .trend .row-trend .info .icons:hover{
    background-color: var(--main-color);

}