:root{
    --ge-p-left :40px;
    --ge-p-right :40px;
    --main-color :#0171F9;
    --second-color: #EE626B ;
    --gray-bg-section :  #eeeeee94;
}
/* Target the whole scrollbar */
::-webkit-scrollbar {
    width: 15px; /* Width of the scrollbar */
}

/* Handle of the scrollbar */
::-webkit-scrollbar-thumb {
    background-color: var(--main-color); /* Color of the scrollbar */
    border-radius: 15px; /* Rounded corners */
}

/* Track (the area the scrollbar runs along) */
::-webkit-scrollbar-track {
    background-color: var(--gray-bg-section); /* Color of the track */
}
