.navbar {
    background-image: url('../../assets/images/page-heading-bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    color: white;
    position: fixed;
    padding-left: 35px;
    padding-right: 35px;
    top: 0;
    width: 100%;

}
.navbar li {
    margin-left: 15px;
}
.navbar li a{
    color: white;
}
.navbar li a.active{
    background-color: blue;
    border-radius: 25px;
}
.navbar li a.signin{
    background-color: rgb(238, 33, 74);
    border-radius: 25px;
}
.navbar-collapse{
    flex-grow: 0;
}
@media(max-width: 775px){
    .navbar li a{
        text-align: center;
        background-color: white;
        color: black;
        border-bottom:  1px solid rgba(0, 0, 0, 0.247);
    }
    .navbar li a.active,
    .navbar li a.signin{
        
        border-radius: 0;
    }
}