.trend-main .section-head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.trend-main .section-head .texts div{
    color: var(--second-color);
    font-weight: 800;
    font-size: 18px;
}
.trend-main .section-head .texts h1{
    margin-top: 15px;
    font-weight: bold;
}
.trend-main .section-head .button a{
    color: white;
    background-color: var(--second-color);
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 20px;
    transition: .4s;
}
.trend-main .section-head .button a:hover{
    background-color: var(--main-color);
}