.fet-content .fet-row{
    flex-grow: 1;
    width: 21%;
    text-align: center;
    background-color: white;
    padding: 40px;
    border-radius: 30px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.253);
}
@media(max-width:992px){
    .fet-content .fet-row{
        width: 45%;
    }
}
.fet-content .fet-row h5{
    padding-top: 40px;
    font-weight: bold;
}
.fet-content .fet-row div {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: var(--main-color);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    transition: .3s;
}
.fet-content .fet-row div:hover {
    background-color: var(--second-color);
}