.main-cat .category .row-category{
    flex: 0 1 calc(20% - 30px);
    background-color: var(--main-color);
    text-align: center;
    color: white;
    border-radius: 20px;
    overflow: hidden;
}
.main-cat .category .row-category h4{
    padding: 10px ;
}
@media (max-width:992px){
    .main-cat .category .row-category{
        flex: 0 1 calc(50% - 30px);
    }
}
@media (max-width:767px){
    .main-cat .category .row-category{
        flex: 0 1 calc(100% - 30px);
    }
}
.main-cat .category .row-category img{
    width: 100%;
}