.main-cat{
    padding: 90px  var(--ge-p-left);
    background-color:var(--gray-bg-section);
    border-radius: 50px;
    margin: 60px 0;
}

.main-cat .head{
    text-align: center;
    margin-bottom: 40px;
}
.main-cat .head h6{
    color: var(--second-color);
}
.main-cat .category{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
}
